.container{
  position: relative;
  width: 100vh;
  height: 100vh;
  margin: 100 auto;
}

.block1{
  position: absolute;
  width: 400px;
  height: 400px;
  margin: 300px ;
  background-color: #fff;
  overflow: hidden;
  
}
.block2{
 position: absolute;
 width: 400px;
 height: 400px;
 transform: rotate(45deg);
 background-color: blue;
 border: 2px solid black;
 overflow: hidden;
 
}
.block3{
  position: absolute;
  width: 400px;
  height: 400px;
  transform: rotate(-45deg);
  background-color: blue;
  border: 2px solid black;
 }
 .hexagon{
   background-color: blanchedalmond;
   width: 300px;
   height: 300px;
   margin: 0 auto;
   position: relative;
 }
 .hexagon_item_1{
   position: absolute;
   width: 100%;
   height: 58%;
   background-color: rgb(25, 81, 185);
   transform: rotate(60deg);
   top: 60px;
   border: 5px solid #000;
 }
 .hexagon_item_2{
  position: absolute;
  width: 100%;
  height: 58%;
  background-color: rgb(25, 81, 185);
  transform: rotate(-60deg);
  top: 60px;
  border: 5px solid #000;
 }
 .hexagon_item_3{
  position: absolute;
  width: 100%;
  height: 58%;
  background-color: rgb(25, 81, 185);
  transform: rotate();
  top: 60px;
  border: 5px solid #000;
 }

.clip{
  width: 100px;
  height: 100px;
  background-color: #000;
  clip-path: polygon(48% 0, 0% 100%, 100% 100%);
}